.container {
  height: 100vh;
  background-color: rgb(252, 220, 170);
  display: flex;
  flex-direction: row;
 
  overflow-x: hidden;
}
.sidebar{
  width: 30vw;
  background-color: rgb(255, 255, 255);
}

.group-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.create{
  font-size: 20px;
  background-color: black;
  color: white;
  width: 70%;
  height: 70%;

}

.group-menu button {
  margin: 5px;
  padding: 15px 50px;
  border: none;
  cursor: pointer;
  border-radius: 20px;
  font-weight: bold;
}

.group-menu button.active {
 color: #000;
  padding: 20px 70px;
}



.note-input {
  
  position: relative;
}

.note-input input {
  padding: 30px 50px 50px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 5%;
  width: 1100px;
}

.note-input button {
  padding: 5px 10px;
  border: none;
  cursor: pointer;
  background-color: #0074d9;
  color: #fff;
  border-radius: 5px;
  font-weight: bold;
  position: static;
}

.notes-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 100px;
}

.note {
  background-color: rgb(252, 220, 170);
 
  border-radius: 5px;
  margin-bottom: 15px;
 
}
.send{
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10%;
  width: 150px;
  height: 80px;
  margin-top: 10px;
 
}


.textbox {
position: absolute;
display: flex;
bottom: 5px;
}


.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-inner {
  background: #fff;
  padding: 30px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.popup-inner input {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
}

.popup-inner button {
  padding: 5px 10px;
  border: none;
  cursor: pointer;
  background-color: #030303;
  color: #fff;
  border-radius: 5px;
  font-weight: bold;
  margin-right: 10px;
}

.popup-inner button:last-child {
  background-color: #ccc;
  color: #000;
}
.profile-pic {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: 50%;
  margin-right: 45px;
  font-weight: bold;
  color: white;
}
.gname{
  font-size:20px;
  margin-left: 35px;
  color: #0c0b0b;
}

.img{
   align-items: center;
   margin-left: 20%;
   margin-top: 5%;
   
}
img{
  width:65%;
  height:60%;
  
}
.head{
  text-align: center;
  margin-top: 10px;
}
.msg{
  text-align: center;
}
.nav-bar{
  background-color:  rgb(250, 215, 159);
  display: flex;
  width:100%;
   position: absolute;
    top: 10px; 
  padding:10px;
}
